<template>
  <div class="con">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span></span>
    </div>
    <div class="box">
      <ul>
        <li>
          <span><i>*</i> : </span>
          <span><el-input v-model="input" placeholder=""></el-input></span>
        </li>
        <li>
          <span><i>*</i> : </span>
          <span> <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select></span>
        </li>
        <li>
          <span><i>*</i> : </span>
          <span><el-date-picker
            v-model="Date1"
            type="date"
            :picker-options="pickerBeginDateBefore"
            placeholder="">
          </el-date-picker> </span>
        </li>
        <li>
          <span><i>*</i> : </span>
          <span><el-date-picker
            v-model="Date2"
            type="date"
            :picker-options="pickerBeginDateAfter"
            placeholder="">
          </el-date-picker> </span>
        </li>
        <li>
          <span><i>*</i> : </span>
          <span class="span mTop10">{{selection}}<button class="dialogBox" @click="department"></button>
          </span>
        </li>
        <li>
          <span><i>*</i> : </span>
          <span class="span mTop6">{{selection2}}<button class="dialogBox" @click="obData"></button>
          </span>
        </li>
        <li class="ks-clear">
          <span> : </span>
          <span>
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4}"
              :maxlength="120"
              placeholder=""
              resize="none"
              v-model="textarea3">
</el-input>
          </span>
        </li>
        <li>
          <router-link to=""><button class="btn3" @click="subMit"></button></router-link>
          <router-link to="/home/AssessmentTask"><button class="btn4"></button></router-link>
        </li>
      </ul>
    </div>
    <!---->
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div class="dlBox">
        <el-tree
          :data="data2"
          show-checkbox
          node-key="id"
          :default-expanded-keys="[1]"
          :default-checked-keys="selectArr"
          :props="defaultProps"
          ref="tree2"
        >
        </el-tree>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="getCheckedNodesObj"> </el-button>
        <el-button @click="centerDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible2"
      width="30%"
      center>
      <div class="dlBox">
        <el-tree
          :data="data3"
          show-checkbox
          node-key="id"
          :default-expanded-keys="[1]"
          :default-checked-keys="select2Arr"
          :props="defaultProps"
          ref="tree"
        >
        </el-tree>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="getCheckedNodes2"> </el-button>
        <el-button @click="centerDialogVisible2 = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script type="es6" scoped>
  import {puHttp} from '../../api/puHttp'
  export default {
    name: 'home',
    data(){
      return {
        centerDialogVisible: false,
        centerDialogVisible2: false,
        options: [{
          value:1,
          label: ''
        }, {
          value:2,
          label: ''
        }, {
          value:3,
          label: ''
        }, {
          value:4,
          label: ''
        }, {
          value:5,
          label: ''
        }],
        value:'',
        Date1:'',
        Date2: '',
        input:'',
        textarea3:'',
        selection:'',
        selection2:'',
        selectArr:[],
        select2Arr:[],
        data2: [{
          id: 1,
          label: '',
          children: []
        }],
        data3: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        pickerBeginDateBefore:{
          disabledDate: (time) => {
            if (this.Date2==='') {
              return time.getTime() < Date.now() - 8.64e7
            } else {
              return time.getTime() > this.Date2 || time.getTime() < Date.now() - 8.64e7
            }
          }
        },
        pickerBeginDateAfter:{
          disabledDate: (time) => {
            if (this.Date1==='') {
              return time.getTime() < Date.now() - 8.64e7
            } else {
              return time.getTime() < this.Date1
            }
          }
        }
      }
    },
    activated(){
      this.value=''
      this.Date1='',
      this.Date2= '',
      this.input='',
      this.textarea3='',
      this.selection='',
      this.selection2='',
      this.seleObject(),
      this.$refs.tree2.setCheckedKeys([]);
      this.$refs.tree.setCheckedKeys([]);
    },
    methods:{
      //
      getCheckedNodesObj(){
        const val2=this.$refs.tree2.getCheckedNodes()
        let str='';
        if(val2.length==1){
          str+=val2[0].label
        }else {
          for (let i=0;i<val2.length;i++){
            str+=(val2[i].label+'')
          }
        }
        this.selection=str
        this.centerDialogVisible=false
      },
      //
      getCheckedNodes2(){
        const val=this.$refs.tree.getCheckedNodes()
        let str2='';
        if(val.length==1){
          str2+=val[0].label
        }else {
          for (let i=0;i<val.length;i++){
            str2+=(val[i].label+'')
          }
        }
        this.selection2=str2
        this.centerDialogVisible2=false
      },
      //
      subMit(){
        if (this.input==""){
          this.openObject("");
          return
        }
        if (this.value==""){
          this.openObject("");
          return
        }
        if (this.Date1==""){
          this.openObject("");
          return
        }
        if (this.Date2==""){
          this.openObject("");
          return
        }
        if (this.selection==""){
          this.openObject("");
          return
        }
        if (this.selection2==""){
          this.openObject("");
          return
        }
        if (this.input.length>40){
          this.openObject("40");
          return
        }
        puHttp.addObj(JSON.stringify({
          assessName:this.input,//
          type:this.value,//
          startTime:this.dateObj(this.Date1),//
          endTime:this.dateObj(this.Date2),//
          require:this.textarea3,//
          department:"",//
          data:this.selection2,//
        })).then((result)=>{
          if (result.result == 1) {
            this.$router.push("/home/AssessmentTask/list");
          }
        }) .catch((error) => {
          this.openObject("")
        })
      },
      addList(){

      },
      seleObject(){
        if (this.data2[0].children.length==0){
          puHttp.department({
          }).then((result)=>{
            result.data.forEach(val=>{
              this.data2[0].children.push({"label":val.GOV_CNAME,"id":val.GOV_ID,})
            })
          }) .catch((error) => {
            this.openObject(",")
          })
        };
        if (this.data3.length==0){
          puHttp.kData({
          }).then((result)=>{
            result.data.forEach(val=>{
              this.data3.push({"label":val.BOOK_NAME,"id":val.BOOK_ID,})
            })
          }) .catch((error) => {
            this.openObject(",")
          })
        }
      },
      //
      department(){
        this.selectArr =this.selection.split("")
        this.centerDialogVisible = true;
      },
      //
      obData(){
        this.select2Arr =this.selection2.split("")
        this.centerDialogVisible2 = true
      }
    },
  }
</script>

<style lang="scss" scoped>
  .con{
    width: 100%;
    min-height: calc(100vh - 120px);
    /*height: 960px;*/
    margin-top: 0 !important;
    background-color: #fff;
  }
  .box{
    width: 100%;
    ul{
      width: 100%;
      margin-top: 30px;
      li{
        width: 100%;
        min-height:60px;
        line-height: 60px;
        position: relative;
        span{
          height: 100%;
          display: inline-block;
          float: left;
          font-size: 14px;
          box-sizing: border-box;
        };
        .span{
          border: 1px solid #dcdfe6;
          width: 27% !important;
          height: 40px;
          margin-left: 10px;
          padding: 0 15px;
          line-height: 40px;
          color: #606266;
          border-radius:4px;
          padding-right:65px !important;
          overflow:hidden;
          white-space:nowrap;
          text-overflow:ellipsis;
          /*margin-top:6px;*/
        }
        .mTop10{
          margin-top:10px;
        }
        .mTop6{
          margin-top: 6px;
        }
        span:first-child{
          width: 35%;
          text-align: right;
          color: #646464;
          padding-right: 10px;
          i{
            color: #e23c3c;
            position: relative;
            left: -3px;
            top: 2px;
          }
        }
        span:last-child{
          width:65%;
          text-align: left;
          color: #5b5b5b;
          padding-left:10px;
          position: relative;
          .el-input,.el-select,.el-textarea{
            width: 42% !important;
          }
        }
      }
      li:last-child{
        margin-top:120px;
        text-align: center;
      }
    }
  }
  .btn3,.btn4{
    width: 90px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    font-size: 14px;
    border: 1px solid #3a81f7;
    border-radius:5px;
    position: relative;
    /*left: 50%;*/
    top: 50%;
    transform: translateY(-50%);
  }
  .btn3{
    background: #4d9cf7;
    color: #fff;
  }
  .btn4{
    background: #edf5fe;
    color: #4d9cf7;
  }
  .btn3:hover,.btn4:hover{
    text-decoration: underline;
  }
  .el-popper{
    top: 295px !important;
  }
</style>



